import React, { useState, useEffect } from 'react';
import {
  ChakraProvider,
  Box,
  Text,
  SimpleGrid,
  Input,
  Button,
  useToast,
  HStack,
  Center,
  Image,
  VStack,
} from '@chakra-ui/react';
import ColorModeSwitcher from './ColorModeSwitcher';
import Logo from './logo.png';
import sysImage from './system.png';
import Versus from './versus.png';

function App() {
  const [score, updateScore] = useState(0);
  const [userCall, newUserCall] = useState(0);
  const [sysCall, newSysCall] = useState(0);
  const [steps,updateSteps] = useState(0);
  const toast = useToast();

  function generate() {
    if (score >= 100) {
      toast({
        title: 'You WON',
        description: 'You WON the Game',
        status: 'success',
        position: 'top',
        duration: 5000,
        isClosable: true,
      });
      document.getElementById('userCall').readOnly = true;
      document.getElementById("submitBtn").disabled = true;
    } else {
      let newCall;
      if (100 - score < 11) {
        newCall = 100 - score;
        toast({
          title: 'System WON',
          description: 'System WON the Game',
          status: 'error',
          position: 'top',
          duration: 5000,
          isClosable: true,
        });
        document.getElementById('userCall').readOnly = true;
        document.getElementById("submitBtn").disabled = true;
      } else {
        newCall = Math.floor(Math.random() * (10 - 1 + 1)) + 1;
      }
      newSysCall(newCall);
      updateScore(score + newCall);
      toast({
        title: 'System Called',
        description: 'Sysem Called ' + newCall,
        status: 'warning',
        duration: 2000,
        isClosable: true,
      });
    }
  }

  useEffect(() => {
    generate();
  }, [steps]);

  function userCalled() {
    if (
      document.getElementById('userCall').value > 0 &&
      document.getElementById('userCall').value < 11
    ) {
      newUserCall(document.getElementById('userCall').value);
      updateScore(score + parseInt(document.getElementById('userCall').value));
      toast({
        title: 'Success',
        description: 'You Called ' + document.getElementById('userCall').value,
        status: 'success',
        duration: 2000,
        isClosable: true,
      });
      document.getElementById('userCall').value = '';
      updateSteps(steps+1)
    } else {
      toast({
        title: 'Error',
        description: 'Only 1-10 Steps Allowed',
        status: 'error',
        duration: 2000,
        isClosable: true,
      });
    }
  }
  return (
    <ChakraProvider>
      <HStack
        mb="4"
        as="header"
        position="fixed"
        justify="space-between"
        p="3"
        px="4%"
        w="100%"
      >
        <Box w={{ base: '50%', md: '30%' }}>
          <Image src={Logo} />
        </Box>
        <Box w={{ md: '40%' }} align="center">
          <Text
            fontSize="2xl"
            fontFamily="'Pacifico', cursive"
            display={{ base: 'none', md: 'block' }}
          >
            Number Game
          </Text>
        </Box>
        <Box w={{ base: '50%', md: '30%' }} align="right">
          <Button
            mr="2"
            colorScheme="orange"
            onClick={() => {
              document.getElementById('userCall').readOnly = false;
              document.getElementById("submitBtn").disabled = false;
              newUserCall(0);
              newSysCall(0);
              updateScore(0);
              updateSteps(steps+1);
            }}
          >
            Restart
          </Button>
          <ColorModeSwitcher></ColorModeSwitcher>
        </Box>
      </HStack>
      <br />
      <Box w={{ md: '60%' }} mx="auto">
        <VStack spacing={{ base: '3vh', md: '10vh' }} w="100%">
          <VStack spacing="3" p="0" w="100%" mt={{ base: '20%', md: '8%' }}>
            <HStack p="0" rounded="md" w={{ base: '90%', md: '75%' }}>
              <SimpleGrid columns="3" p="0" spacing="0" rounded="2" w="100%">
                <Button rounded="0" colorScheme="red" p="4" w="100%" h="100%">
                  <VStack>
                    <Text fontSize={{ base: 'sm', md: 'lg' }} fontWeight="600">
                      PC's Call
                    </Text>
                    <Text fontSize="3xl" fontWeight="600">
                      {sysCall}
                    </Text>
                  </VStack>
                </Button>
                <Button rounded="0" p="4" w="100%" colorScheme="blue" h="100%">
                  <VStack p="4">
                    <Text fontSize={{ base: 'sm', md: 'lg' }} fontWeight="600">
                      Total Score
                    </Text>
                    <Text fontSize="3xl" fontWeight="600">
                      {score}
                    </Text>
                  </VStack>
                </Button>
                <Button rounded="0" p="4" w="100%" h="100%" colorScheme="green">
                  <VStack p="4">
                    <Text fontSize={{ base: 'sm', md: 'lg' }} fontWeight="600">
                      Your Call
                    </Text>
                    <Text fontSize="3xl" fontWeight="600">
                      {userCall}
                    </Text>
                  </VStack>
                </Button>
              </SimpleGrid>
            </HStack>
          </VStack>
          <SimpleGrid
            mx="auto"
            w={{ base: '90%', md: '75%' }}
            mt={{ base: '3%', md: '4%' }}
            columns={{ base: '1', md: '3' }}
          >
            <Center>
              <Image w={{ base: '70%', md: '100%' }} src={sysImage} />
            </Center>
            <Center>
              <Image
                display={{ base: 'none', md: 'block' }}
                w="40%"
                src={Versus}
              ></Image>
            </Center>
            <Center>
              <VStack spacing={{ base: '2', md: '3' }}>
                <Input
                  type="number"
                  border="2px solid"
                  rounded="0"
                  fontSize={{ md: '3xl' }}
                  p="0"
                  py="3"
                  id="userCall"
                  w={{ base: '80%', md: '100%' }}
                  h={{ md: '15vh' }}
                  placeholder="Your Call"
                  textAlign="center"
                ></Input>
                <Button
                  size="lg"
                  rounded="0"
                  id="submitBtn"
                  onClick={userCalled}
                  colorScheme="teal"
                  w={{ md: '100%' }}
                >
                  Submit
                </Button>
              </VStack>
            </Center>
          </SimpleGrid>
          <Text
            align="center"
            mx="auto"
            w="90%"
            mt="3%"
            fontSize={{ base: 'sm', md: 'lg' }}
          >
            Call between 1-10 and the first to reach 100 WINS
          </Text>
        </VStack>
      </Box>
    </ChakraProvider>
  );
}

export default App;
